
import React, { useEffect, useState } from 'react'
import { makeStyles } from '@material-ui/styles';
import { Grid } from '@material-ui/core';
// import { connect } from 'react-redux'
// import { loadEmployees, saveEmployee } from './../../redux/actions/employee.actions'
import { AccountProfile, AccountDetails } from './components';
import { useQuery, useMutation } from '@apollo/react-hooks';
// import EmployeeQueryBuilder from './../../graphql/queryBuilder'
import { FETCH_EMPLOYEES, ADD_EMPLOYEE } from 'graphql/queries';
import Loader from 'components/Loader';


const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(4)
  }
}));

const AddUser = () => {

  const classes = useStyles();
  const [state, setState] = useState([])

  const { loading, data } = useQuery(FETCH_EMPLOYEES);
  const [onAddEmployeeHandler, { data1, loading1, error }] = useMutation(ADD_EMPLOYEE, {
    refetchQueries: [{ query: FETCH_EMPLOYEES }]
  });

  useEffect(() => {
    if (data)
      setState(data)
  }, [data]);

  const saveEmployee = (objEmployee) => {
    onAddEmployeeHandler({ variables: objEmployee });
  }

  return (
    <div className={classes.root}>
      {loading ? <Loader/> : <p>{JSON.stringify(state)}</p>}

      <Grid
        container
        spacing={4}
      >
        <Grid
          item
          lg={4}
          md={6}
          xl={4}
          xs={12}
        >
          <AccountProfile />
        </Grid>
        <Grid
          item
          lg={8}
          md={6}
          xl={8}
          xs={12}
        >
          {/* <AccountDetails /> */}
          <AccountDetails onEmplyeeSave={saveEmployee} />
        </Grid>
      </Grid>
    </div>
  )
};

export default AddUser;



