
import { ApolloClient } from "apollo-client";
import { InMemoryCache } from "apollo-cache-inmemory";
import { createHttpLink } from "apollo-link-http";
import { setContext } from 'apollo-link-context';
import app from "constants/app.url";


const configureGraphql = () => {

    const authLink = setContext((_, { headers }) => {
        // get the authentication token from local storage if it exists
        const accessToken = localStorage.getItem('accessToken');
        const refreshToken = localStorage.getItem('refreshToken');
        return {
            headers: {
                ...headers,
                ["x-token"]: accessToken ? `Bearer ${accessToken}` : "",
                ["x-refresh-token"]: refreshToken ? `Refresh ${refreshToken}` : "",
            }
        }
    });

    const httpLink = createHttpLink({ uri: app.graphqlEndPoint });
    const cache = new InMemoryCache();
    const client = new ApolloClient({ link: authLink.concat(httpLink), cache });
    return client;
}

export default configureGraphql;