import axios from "axios";
// import { useDispatch } from 'react-redux';

import * as types from '../constants/ActionTypes';


export const appLoading = (text) => ({
    type: types.APP_LOADING,
    text
})


export const getEmployees = (id) => ({
    type: types.GET_EMPLOYEES,
    id
})
export const loadEmployeesSuccess = (payload) => ({
    type: types.LOAD_EMPLOYEES_SUCCESS,
    payload
})

export const loadEmployees = () => {
    return (dispatch) => {
        return axios.get('http://localhost:3001/get-all-employees')
            .then((response) => {
                if (response.data)
                    dispatch(loadEmployeesSuccess(response.data))
            }).catch(error => {
                console.log('error =>', error);
                //Add a dispatch here
            });
    }
}


