import { SAVE_EMPLOYEE, SAVE_EMPLOYEE_FAILURE, SAVE_EMPLOYEE_STARTED, SAVE_EMPLOYEE_SUCCESS, LOAD_EMPLOYEES_SUCCESS } from '../constants/ActionTypes'
// const initialState = { employee: [] }
// const initialState = ["pravin"];
const initialState = [];

export const employeeReducer = (state = initialState, action) => {
    const { type, payload } = action;
    console.log("employeeReducer = ", type, "State =", state)
    switch (type) {
        case SAVE_EMPLOYEE:
            return [...state, action.name]

        case SAVE_EMPLOYEE_STARTED:
            return {
                ...state,
                loading: true
            };
        case SAVE_EMPLOYEE_SUCCESS:
            return {
                ...state,
                loading: false,
                error: null,
                todos: [...state.todos, action.payload]
            };
        case SAVE_EMPLOYEE_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.payload.error
            };
        case LOAD_EMPLOYEES_SUCCESS:
            console.log("FINALLYY SUCCESS ME AAYA re", state, payload)
            return [...payload.employeeList];
        default:
            return state
    }

}
