import React, { useState ,useEffect} from 'react';
import { makeStyles } from '@material-ui/styles';
import { UsersToolbar, UsersTable } from './components';
import mockData from './data';
import { connect } from 'react-redux';
import { loadEmployees } from "./../../redux/actions/employee.actions"
import { useQuery, useMutation } from '@apollo/react-hooks';
import gql from "graphql-tag";
import { FETCH_EMPLOYEES } from 'graphql/queries';


const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(3)
  },
  content: {
    marginTop: theme.spacing(2)
  }
}));

const EmployeeList = (props) => {
  const classes = useStyles();

  const { loading, data } = useQuery(FETCH_EMPLOYEES);
  // const { loading, data } = useQuery(FETCH_EMPLOEES);

  // useEffect(() => {
  //   if (!props.employees.employeeReducer.length)
  //     props.loadEmployees();
  // }, []);

  const [users] = useState(mockData);

  return (
    <div className={classes.root}>
       {loading ? <p>Loading</p> : <p>{JSON.stringify(data)}</p>}
       {/* {JSON.stringify(props.employees.employeeReducer)} */}
      <UsersToolbar />
      <div className={classes.content}>
        <UsersTable users={users} />
        {/* <UsersTable users={props.employees.employeeReducer} /> */}
      </div>
    </div>
  );
};

const mapStateToProps = state => {
  return {
    employees: state
  }
}

const mapDispatchToProps = dispatch => {
  return {
    loadEmployees: () => dispatch(loadEmployees())
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(EmployeeList)


