import gql from 'graphql-tag';
export const FETCH_EMPLOYEES = gql`{
    employees {
      id:_id
      name:firstName
      lastName
      email
      phone
      state
      country
    }
  }`

export const ADD_EMPLOYEE = gql`
  mutation ($firstName: String!, $lastName: String, $email: String, $phone: String!,$state:String,$country:String) {
      addEmployee(firstName:$firstName,lastName:$lastName,email:$email,phone:$phone,state:$state,country:$country) {
           firstName
           lastName
       }
  }`;
//check for resolver GraphQLNonNull field type and based on that provide input 

export const UPDATE_EMPLOYEE = gql`{
    employees {
      lastName,
      firstName
    }
  }`

export const DELETE_EMPLOYEE = gql`{
    employees {
      lastName,
      firstName
    }
  }`